html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

.header {
    padding: 0 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    height: 90px;
    left: 0;
    right: 0;
    background: white;
    box-shadow: 0px 2px 16px 0px rgba(0, 0, 0, 0.31);
}

.header__name {
    font-family: 'Pacifico', system-ui;
    font-size: 32px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}


.header__menu-item {
    font-family: Rubik;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150.4%; /* 24.064px */
    text-transform: capitalize;
    margin-right: 24px;
    color: black;
    text-decoration: none;
}

.header__menu-item:hover {
    text-decoration: underline;
    cursor: pointer;
}

.main-bg {
    background: url('./assets/bg.png');
    position: absolute;
    top: 90px;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: -1;
    background-size: cover;
    overflow: auto;
}

.main-container {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.main-content {
    margin-top: 10%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.main {
    backdrop-filter: blur(5px);
}

.main__name1 {
    text-align: center;
    font-family: Rubik;
    font-size: 30px;
    font-style: normal;
    font-weight: 800;
    letter-spacing: 10px;
    color: #EA6D67;
}

.main__name2 {
    text-align: center;
    font-family: 'Pacifico', system-ui;
    font-weight: 400;
    font-size: 30px;
    color: #A0222E;
    margin-bottom: 20px;
}

.main__name3 {
    text-align: center;
    font-family: 'Rubik', system-ui;
    font-weight: 400;
    font-size: 15px;
    color: #EA6D67;
}

.activities {
    padding-top: 10%;
}

.activities__name {
    text-align: center;
    font-family: 'Pacifico', system-ui;
    font-size: 40px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    color: #A0222E;
}

.activities__images {
    display: flex;
    align-items: center;
    padding: 46px;
    flex-wrap: wrap;
    justify-content: center;
}

.activities__image {
    /*width: 244px;*/
    height: 244px;
    border-radius: 16px;
    object-fit: contain;
    margin: 10px;
}

.about {
    padding-top: 10%;
    max-width: 1024px;
    margin: 0 auto;
}

.about__inner {
    background: white;
    box-shadow: 0px 2px 16px 0px rgba(0, 0, 0, 0.31);
    border-radius: 32px;
    padding: 20px 20px 40px;
}

.about__name {
    text-align: center;
    font-family: 'Pacifico', system-ui;
    font-size: 40px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    color: #A0222E;
}

.about__text {
    text-align: center;
    font-family: 'Rubik', system-ui;
    font-size: 25px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    color: #EA6D67;
    margin-bottom: 10px;
}

.drawer__content {
    background: white;
    height: 100%;
}

.drawer__menu {
    display: flex;
    flex-direction: column;
    padding-left: 10px;
    padding-top: 10px;
}

.drawer__menu-item {
    font-family: Rubik;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150.4%; /* 24.064px */
    text-transform: capitalize;
    margin-right: 24px;
    color: black;
    text-decoration: none;
}

.drawer__menu-item:hover {
    text-decoration: underline;
    cursor: pointer;
}

.header__icon svg {
    fill: #EA6D67;
}

@media screen and (min-width: 320px) {
    .about__inner {
        box-shadow: none;
        /*border-radius: 0;*/
        padding-bottom: 10px;
    }

    .activities {
        /*background: white;*/
    }

    .about__text, .about__text {
        font-size: 14px;
    }

    .about__name, .activities__name {
        font-size: 20px;
    }

    .main {
        backdrop-filter: none;
    }

    .activities {
        padding: 0;
    }

    .activities__images {
        padding: 0;
    }

    .activities__image {
        width: 100%;
        margin: 0;
        margin-bottom: 10px;
    }
}

@media screen and (min-width: 480px) {
    .main__name1 {
        font-size: 40px;
    }

    .main__name2 {
        font-size: 40px;
    }

    .main__name3 {
        font-size: 20px;
    }

    .about__inner {
        box-shadow: none;
        padding-bottom: 10px;
        /*border-radius: 0;*/
    }

    .activities {
        /*background: white;*/
    }

    .about__text, .about__text {
        font-size: 18px;
    }

    .about__name, .activities__name {
        font-size: 30px;
    }

    .activities__image {
        margin-bottom: 10px;
    }
}

@media screen and (min-width: 768px) {
    .main__name1 {
        font-size: 60px;
    }

    .main__name2 {
        font-size: 60px;
    }

    .main__name3 {
        font-size: 40px;
    }

    .main {
        backdrop-filter: blur(5px);
    }

    .about__inner {
        box-shadow: 0px 2px 16px 0px rgba(0, 0, 0, 0.31);
        border-radius: 32px;
        padding: 20px 20px 40px;
    }

    .activities {
        padding-top: 10%;
        background: transparent;
    }

    .activities__images {
        padding: 20px;
    }

    .activities__image {
        width: auto;
        margin: 10px;
    }
}

@media screen and (min-width: 1024px) {
    .main__name1 {
        font-size: 80px;
    }

    .main__name2 {
        font-size: 80px;
    }

    .main__name3 {
        font-size: 60px;
    }

    .about__text, .about__text {
        font-size: 25px;
    }

    .about__name, .activities__name {
        font-size: 35px;
    }
}

@media screen and (min-width: 1280px) {
    .main__name1 {
        font-size: 100px;
    }

    .main__name2 {
        font-size: 100px;
    }

    .main__name3 {
        font-size: 80px;
    }

    .about__text, .about__text {
        font-size: 30px;
    }

    .about__name, .activities__name {
        font-size: 40px;
    }
}

@media screen and (min-width: 1440px) {
    .main__name1 {
        font-size: 120px;
    }

    .main__name2 {
        font-size: 120px;
    }

    .main__name3 {
        font-size: 100px;
    }

    .about__text, .about__text {
        font-size: 35px;
    }

    .about__name, .activities__name {
        font-size: 45px;
    }
}

@media screen and (min-width: 1600px) {
    .main__name1 {
        font-size: 140px;
